/*
    Author: GRADI KAYAMBA
    width: calc(20% + 100px) -  to calculate some related size to its parent
    colors from dark to light: #190934, #300D6E, #5918C9, #905BEC, #E9DFFB
*/
@charset "utf-8";
:root {
    --main-bg-facto-color-one: #0F0830;
    --main-bg-facto-color-two: #664AE0;
}
*, 
*:before, 
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}
body { margin:0; }
img { border:0; }

::-webkit-scrollbar { width: 17px; }
::-webkit-scrollbar-button { background-color: #664AE0; border: 2px solid white; }
::-webkit-scrollbar-thumb { background-color: #4A29DB; border: 2px solid white; }
::-webkit-scrollbar-thumb:hover { background-color: #664AE0; border: 3px solid white; }
::-webkit-scrollbar-track { background-color: white; }

.Container,
.row_inner {
    display: flex;
    flex-wrap: wrap;
}

.Box {
    padding: 20px;
    flex-basis: 50%;
    /*border: 1px solid white;*/
}
.box_3{
    flex-basis: 33.333%;
}
.box_expand { 
    flex-basis:100%; 

}
.Page_box {
    width: 24%;
    height:200px;
    border: 3px solid black;
    border-radius: 5px;
    display: inline-block;
    margin: 5px;
    text-transform: uppercase;
    text-align: center;
}
.row_float_right { 
    justify-content: flex-end; 

}
.Aside {
    flex-basis: 20%;
    background-color: #f9f8fe;
    overflow:auto;
    height:1000px;
    overflow:auto;
}
.header {
    border: 1px solid black;
    background-color: var(--main-bg-facto-color-one);
    color: #905BEC;
}
.Main {
    color:black;
}
.Main, 
.Footer {
    flex-basis:80%;
    color:black;

}
.Footer{
    color:white;
    background-color: var(--main-bg-facto-color-one);
}

.Footer section:last-child {
    text-align: center;
    border-top: 1px solid #905BEC;
}
.container {
    text-align: center;
}

.Button {
    border: 1px solid var(--main-bg-facto-color-one);
    background-color: #2B1787;
    padding: 5px 10px;
    margin-bottom: 20px;
    color: white;
}
.Button:hover {
    background: var(--main-bg-facto-color-one);
    color: white;
    transition: .5s;
}

.accordion .title {
    padding: 10px 30px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    color: white;
    border: 1px solid #f9f8fe;
    border-left: 2px solid #f9f8fe;
    border-right: 2px solid #f9f8fe;
    background: var(--main-bg-facto-color-two);
    text-align: left;
}
    .accordion .title::after {
        content: " +";
        color: white;
        transition: transform .5s ease-in-out;
        right: 30px;
    }
    .accordion .title.is_expanded {
        transition: background .5s;
        background: var(--main-bg-facto-color-one);
    }
    .accordion .title.is_expanded::after {
        content: " -";
        transform: rotate(-360deg);
    }
    .accordion .content {
    overflow: hidden;
    max-height: 0;
    transition: max-height .5s;


}
    .accordion .content p {
        padding: 30px 0;
        margin: 0;
        opacity: 0;
        transition: .5s;
    }
    .accordion .total {
        float: right;
        font-weight: bold;
        color: #5918C9;
    }
    .accordion ul {
        list-style:none;
        text-align:left;
        text-transform:capitalize;
        
    }

.content.is_expanded {
    max-height: initial;
    overflow: hidden;
}
.content.is_expanded p {
    opacity: 1;
}

.content.is_expanded p:after {
    width: 100%;
    height: 10px;
    background: var(--main-bg-facto-color-one);
    content: '';
}
